.hero-container {
    position: relative;
    width: 100%;
    height: 680px; /* Adjust height as needed */
}

.background-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../../assets/hero.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    z-index: 1; /* Ensure the background image is behind the text */
    &:hover{
        filter: brightness(30%);
        transition: .5s ease;
    }
}

.hero-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    z-index: 2; /* Ensure the text is above the background image */
    color: #ffffff; /* Set text color */
}

.hero-text h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
    color: #b50909ed;
}

.hero-text p {
    font-size: 1.5rem;
    margin-bottom: 2rem;
    color: #d5770ced;
    font-weight: 700;
}

.hero-text button {
    background-color: #c50c0cf0;
    color: #f0e9e9;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 1.2rem;
    cursor: pointer;
    transition: background-color 0.3s;
    left: -20px;
    position: relative;
    transition: .5s ease;
}

.hero-text button:hover {
    background-color:black;
    color:white

}

/* Responsive styles */
@media screen and (max-width: 768px) {
    .hero-text {
        width: 80%;
    }

    .hero-text h1 {
        font-size: 2.5rem;
    }

    .hero-text p {
        font-size: 1.2rem;
    }

    .hero-text button {
        font-size: 1rem;
    }
}
